import { createRouter, createWebHashHistory } from "vue-router"
import IndexPage from '@/pages/index/IndexPage'
import OurGroup from '@/pages/our-group/OurGroup'
import CompactReport from '@/pages/compact-report/CompactReport'
import CrossMediaAnalysis from '@/pages/cross-media-analysis/CrossMediaAnalysis'
import AISafety from '@/pages/ai-safety/AISafety'

const routes = [
  { path: "/", redirect: "/index" },
  {
    path: "/index",
    component: IndexPage
  }, {
    path: "/our-group",
    component: OurGroup
  }, {
    path: "/compact-report",
    component: CompactReport
  }, {
    path: "/cross-media-analysis",
    component: CrossMediaAnalysis
  }, {
    path: "/ai-safety",
    component: AISafety
  }
]

export const router = createRouter({
    history: createWebHashHistory(),
    routes: routes,
    scrollBehavior() {
        return { left: 0, top: 0 };
    },
})
