<template>
    <div class="sl-container">
        <div v-for="item in list" v-bind:key="item.id">
            <li v-html="item"></li>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'SimpleList',
        props: {
            list: {
                type: Array,
                default: () => {return []}
            }
        }
    }
</script>

<style scoped>
	.sl-container {
		box-shadow: 0 6px 5px -7px gray;
		padding: 30px 1%;
		color: #555;
	}
</style>