                <div class="lc-cover"><img v-bind:src="item.coverUrl" /></div>
                <div>
                    <div class="lc-title">{{item.title}}</div>
                    <div class="lc-info">{{item.info}}</div>
                    <div class="lc-link-box">
                        <div class="lc-publish">{{item.publish}}</div>
                        <a class="lc-link" v-if="item.pdfUrl != null" v-bind:href="item.pdfUrl">PDF</a>
                        <a class="lc-link" v-if="item.codeUrl != null" v-bind:href="item.codeUrl">CODE</a>
                    </div>
                </div>
            </div>
        </div>
