<template>
    <p class="l-container">
        <span class="l-box" v-for="(item, idx) in list">
            <a href="item.url" class="l-link">{{item.name}}</a>
            <span v-if="idx != list.length - 1" class="sep"> | </span>
        </span>
    </p>
</template>

<script>
    export default {
        name: 'LinkBox',
        props: {
            list: {
                type: Array,
                default: () => {return []}
            }
        }
    }
</script>

<style scoped>
	.l-container {
        float: left;
        word-break: break-all;
        margin: 0 0 10px;
	}

	.l-link {
		color: #0088CC;
	}

    .sep {
        margin: 0 2px 0 0;
    }
</style>